
'use client';

import { useEffect, useState } from 'react';
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from "@/components/ui/card";
import { ArrowRight, Lightbulb, ShieldAlert, Loader2, Brain, Briefcase, ListChecks, Info, Bell } from "lucide-react"; // Removed Map
import Image from "next/image";
import Link from "next/link";
import { getPersonalizedInsights, type PersonalizedInsightsOutput } from './actions';
import type { JobSubmission } from '@/types';
import { addNotifications } from '@/lib/notification-store'; 


const getJobHistory = (): JobSubmission[] => {
  if (typeof window === 'undefined') return [];
  const history = localStorage.getItem('jobSubmissionHistory');
  return history ? JSON.parse(history) : [];
};

export default function HomePage() {
  const [insights, setInsights] = useState<PersonalizedInsightsOutput | null>(null);
  const [isLoadingInsights, setIsLoadingInsights] = useState(true);
  const [jobHistoryExists, setJobHistoryExists] = useState(false);

  useEffect(() => {
    const fetchAndSetInsights = () => {
      const history = getJobHistory();
      if (history.length > 0) {
        setJobHistoryExists(true);
        setIsLoadingInsights(true); 
        getPersonalizedInsights(history)
          .then(data => {
            setInsights(data);
            const newNotifications = [];
            if (data.overallObservations) {
              newNotifications.push({
                title: "New Career Observation",
                message: data.overallObservations.substring(0, 150) + (data.overallObservations.length > 150 ? "..." : ""),
                type: 'observation' as const,
                link: '/' 
              });
            }
            data.careerRecommendations.forEach(rec => {
              newNotifications.push({
                title: `Career Recommendation: ${rec.careerTitle}`,
                message: rec.reasoning.substring(0, 100) + (rec.reasoning.length > 100 ? "..." : ""),
                type: 'recommendation' as const
              });
            });
            if (data.suggestedReminders) {
              data.suggestedReminders.forEach(reminder => {
                 newNotifications.push({
                   title: reminder.title,
                   message: reminder.message,
                   type: reminder.type,
                 });
              });
            }
            if (newNotifications.length > 0) {
              addNotifications(newNotifications);
            }
          })
          .catch(error => {
            console.error("Failed to load personalized insights:", error);
            setInsights(null); 
          })
          .finally(() => setIsLoadingInsights(false));
      } else {
        setIsLoadingInsights(false);
        setJobHistoryExists(false);
        setInsights(null); 
      }
    };

    fetchAndSetInsights(); // Initial fetch

    // Listen for custom event
    window.addEventListener('jobHistoryUpdated', fetchAndSetInsights);

    return () => {
      window.removeEventListener('jobHistoryUpdated', fetchAndSetInsights);
    };
  }, []);


  return (
    <div className="container mx-auto py-8">
      <section className="text-center mb-12">
        <h1 className="text-4xl font-bold tracking-tight text-primary sm:text-5xl md:text-6xl">
          Welcome to AI Career Compass
        </h1>
        <p className="mt-6 max-w-3xl mx-auto text-lg leading-8 text-muted-foreground">
          Navigate your future in an AI-driven world. Discover how AI impacts jobs, identify future-proof skills, and get personalized guidance to achieve your career goals.
        </p>
      </section>

      {isLoadingInsights && jobHistoryExists && (
        <Card className="shadow-lg mb-12 animate-pulse">
          <CardHeader>
            <CardTitle className="text-2xl text-primary flex items-center">
              <Loader2 className="mr-2 h-6 w-6 animate-spin" />
              Loading Your Personalized Insights...
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="h-4 bg-muted rounded w-3/4"></div>
            <div className="h-4 bg-muted rounded w-1/2"></div>
            <div className="h-4 bg-muted rounded w-2/3"></div>
          </CardContent>
        </Card>
      )}

      {!isLoadingInsights && insights && jobHistoryExists && (
        <section className="mb-12">
          <Card className="shadow-xl border-accent">
            <CardHeader>
              <CardTitle className="text-2xl text-primary flex items-center">
                <Brain className="mr-3 h-7 w-7 text-accent" />
                Your Personalized Career Insights
              </CardTitle>
              <CardDescription>
                Based on your assessed job descriptions, here are some tailored observations and recommendations.
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div>
                <h3 className="text-lg font-semibold text-primary mb-2 flex items-center">
                  <Info className="mr-2 h-5 w-5 text-accent" />
                  Overall Observations
                </h3>
                <p className="text-muted-foreground whitespace-pre-wrap">{insights.overallObservations}</p>
              </div>

              {insights.careerRecommendations && insights.careerRecommendations.length > 0 && (
                <div>
                  <h3 className="text-lg font-semibold text-primary mb-3 flex items-center">
                    <Briefcase className="mr-2 h-5 w-5 text-accent" />
                    Career Suggestions
                  </h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {insights.careerRecommendations.map((rec, index) => (
                      <Card key={index} className="bg-secondary/30">
                        <CardHeader>
                          <CardTitle className="text-primary text-lg">{rec.careerTitle}</CardTitle>
                        </CardHeader>
                        <CardContent>
                          <p className="text-sm text-muted-foreground mb-2">{rec.reasoning}</p>
                          <h4 className="text-sm font-medium text-primary mb-1">Relevant Skills:</h4>
                          <ul className="list-disc list-inside text-sm text-muted-foreground">
                            {rec.relatedSkills.map(skill => <li key={skill}>{skill}</li>)}
                          </ul>
                        </CardContent>
                      </Card>
                    ))}
                  </div>
                </div>
              )}

              {insights.learningPrograms && insights.learningPrograms.length > 0 && (
                <div>
                  <h3 className="text-lg font-semibold text-primary mb-3 flex items-center">
                    <Lightbulb className="mr-2 h-5 w-5 text-accent" />
                    Recommended Learning Programs
                  </h3>
                  <div className="space-y-3">
                    {insights.learningPrograms.map((prog, index) => (
                      <div key={index} className="p-3 border rounded-md">
                        <h4 className="font-medium text-primary">{prog.programName} <span className="text-xs text-muted-foreground">({prog.programType})</span></h4>
                        <p className="text-sm text-muted-foreground">{prog.relevance}</p>
                        {prog.details && <p className="text-xs text-muted-foreground/80 mt-1">Details: {prog.details}</p>}
                      </div>
                    ))}
                  </div>
                </div>
              )}
              
              {insights.suggestedReminders && insights.suggestedReminders.length > 0 && (
                <div>
                  <h3 className="text-lg font-semibold text-primary mb-3 flex items-center">
                    <Bell className="mr-2 h-5 w-5 text-accent" />
                    Things to Keep in Mind
                  </h3>
                  <div className="space-y-3">
                    {insights.suggestedReminders.map((reminder, index) => (
                      <div key={index} className="p-3 border rounded-md bg-blue-50 dark:bg-blue-900/30 border-blue-200 dark:border-blue-700">
                        <h4 className="font-medium text-primary">{reminder.title}</h4>
                        <p className="text-sm text-muted-foreground">{reminder.message}</p>
                      </div>
                    ))}
                  </div>
                </div>
              )}


              {insights.nextSteps && insights.nextSteps.length > 0 && (
                <div>
                  <h3 className="text-lg font-semibold text-primary mb-3 flex items-center">
                    <ListChecks className="mr-2 h-5 w-5 text-accent" />
                    Actionable Next Steps
                  </h3>
                  <ul className="list-disc list-inside space-y-1 text-muted-foreground">
                    {insights.nextSteps.map((step, index) => <li key={index}>{step}</li>)}
                  </ul>
                </div>
              )}
            </CardContent>
             <CardFooter>
              <Button asChild variant="link" className="text-accent">
                <Link href="/automation-risk">Assess another job description to refine insights <ArrowRight className="ml-2 h-4 w-4" /></Link>
              </Button>
            </CardFooter>
          </Card>
        </section>
      )}

      {!jobHistoryExists && !isLoadingInsights && (
         <section className="mb-12">
          <Card className="shadow-lg overflow-hidden rounded-xl bg-gradient-to-br from-primary to-blue-700 text-primary-foreground">
            <CardContent className="p-8 text-center">
                <Brain className="mx-auto h-12 w-12 mb-4 text-accent" />
                <CardTitle className="text-2xl font-semibold mb-2">Unlock Personalized Insights!</CardTitle>
                <CardDescription className="text-lg text-primary-foreground/90 mb-6">
                  Start by assessing a job description in our "Automation Risk" tool. Your history will power tailored career recommendations right here on your dashboard.
                </CardDescription>
                <Button asChild size="lg" className="bg-accent text-accent-foreground hover:bg-accent/90">
                  <Link href="/automation-risk">
                    Assess Your First Job <ArrowRight className="ml-2 h-5 w-5" />
                  </Link>
                </Button>
            </CardContent>
          </Card>
        </section>
      )}


      <section className="mb-12">
        <Card className="shadow-lg overflow-hidden rounded-xl">
          <div className="md:flex">
            <div className="md:w-1/2">
              <Image
                src="https://storage.googleapis.com/project-spark-b2767.appspot.com/users%2F19488421835786221147%2Flabs%2Ff58ee588-ac0d-400d-9b88-1d21bd7c963a%2Fimage_f34d1937-25e2-41ba-ac5c-5b23d90f679b"
                alt="AI and Future of Work"
                width={800}
                height={600}
                className="object-cover h-full w-full"
                data-ai-hint="futuristic technology"
              />
            </div>
            <div className="md:w-1/2 p-8 flex flex-col justify-center bg-card">
              <h2 className="text-2xl font-semibold text-primary mb-4">Why AI Career Compass?</h2>
              <p className="text-muted-foreground mb-3">
                The rise of AI brings both incredible opportunities and significant challenges. It can optimize processes and create new industries, but it's also projected to displace jobs.
              </p>
              <p className="text-muted-foreground">
                Our goal is to demystify these changes, empower you with knowledge, and provide actionable steps to navigate the evolving job market.
              </p>
            </div>
          </div>
        </Card>
      </section>

      <section>
        <h2 className="text-3xl font-semibold text-center text-primary mb-8">Explore Our Tools</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <FeatureCard
            icon={<ShieldAlert className="h-10 w-10 text-accent" />}
            title="Automation Risk Assessment"
            description="Analyze job descriptions to understand the likelihood of automation and prepare accordingly."
            link="/automation-risk"
          />
          <FeatureCard
            icon={<Lightbulb className="h-10 w-10 text-accent" />}
            title="Future Skills Identifier"
            description="Identify key skills needed for future-proof careers based on current trends and expert forecasts."
            link="/future-skills"
          />
          <FeatureCard
            icon={<Briefcase className="h-10 w-10 text-accent" />}
            title="Explore Careers"
            description="Discover various career paths, understand their AI impact, and find resources to guide your journey."
            link="/careers"
          />
        </div>
      </section>
    </div>
  );
}

interface FeatureCardProps {
  icon: React.ReactNode;
  title: string;
  description: string;
  link: string;
}

function FeatureCard({ icon, title, description, link }: FeatureCardProps) {
  return (
    <Card className="shadow-md hover:shadow-xl transition-shadow duration-300 rounded-lg">
      <CardHeader className="items-center text-center">
        {icon}
        <CardTitle className="mt-4 text-primary">{title}</CardTitle>
      </CardHeader>
      <CardContent className="text-center">
        <CardDescription>{description}</CardDescription>
        <Button asChild variant="link" className="mt-4 text-accent">
          <Link href={link}>
            Learn More <ArrowRight className="ml-2 h-4 w-4" />
          </Link>
        </Button>
      </CardContent>
    </Card>
  );
}
